<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/adminhome'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>投诉处理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-table
        :data="this.receive"
        style="width: 100%"
        :row-class-name="tableRowClassName"
        border
        fit>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row>
              <el-col :span="2">
                <span style="text-align: center"><h1>投诉内容:</h1></span>
              </el-col>
              <el-col :span="19">
                <el-input v-model="scope.row.content" type="textarea" disabled></el-input>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column label="投诉编号" prop="id" align="center">

        </el-table-column>
        <el-table-column label="房间号" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.userEntity.houseEntity.buildingId}} 栋 {{scope.row.userEntity.houseEntity.floor}} 层 {{scope.row.userEntity.houseEntity.number}} 号 </span>
          </template>
        </el-table-column>
        <el-table-column label="户主姓名" prop="userEntity.name" align="center">

        </el-table-column>
        <el-table-column label="联系电话" prop="userEntity.phoneNumber" align="center">

        </el-table-column>
        <el-table-column label="提交时间" align="center">
          <template slot-scope="scope">
            <span>{{getTime(scope.row.time)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="投诉状态" prop="state" align="center">
          <template slot-scope="scope">
            <span>{{getState(scope.row.state)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button-group>
              <el-tooltip class="item" effect="dark" content="接收投诉" placement="top-end">
                <el-button type="warning" @click="receiveComplaint(scope.row)" icon="el-icon-check" :disabled="judgeBtn(scope.row.state, 1)"></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="完成投诉处理" placement="top-end">
                <el-button type="primary" @click="finishComplaint(scope.row)" icon="el-icon-check" :disabled="judgeBtn(scope.row.state, 2)"></el-button>
              </el-tooltip>
            </el-button-group>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Complaints',
  data () {
    return {
      receive: [],
      form: {
        userId: '',
        houseId: '',
        content: ''
      },
      userMsg: {

      }
    }
  },
  created () {
    this.load()
  },
  methods: {
    async load () {
      const {data: res} = await this.$http.get('/property_management/complaints/listall')
      console.log(res)
      this.receive = res.data
    },
    tableRowClassName (row, rowIndex) {
      if (row.row.state === 0) {
        return 'warning-row'
      } else if (row.row.state === 1) {
        return 'danger-row'
      } else if (row.row.state === 2) {
        return 'success-row'
      }
    },
    getState (state) {
      if (state === 0) {
        return '未接收'
      } else if (state === 1) {
        return '处理中'
      } else if (state === 2) {
        return '已处理'
      }
    },
    judgeBtn (state, local) {
      if (state === 0 && local === 1) {
        return false
      } else if (state === 1 && local === 2) {
        return false
      } else {
        return true
      }
    },
    async receiveComplaint (row) {
      const {data: res} = await this.$http.get('/property_management/complaints/receive', {
        params: {
          'id': row.id
        }
      })
      console.log(res)
      this.$message.success(res.msg)
      this.load()
    },
    async finishComplaint (row) {
      const {data: res} = await this.$http.get('/property_management/complaints/finish', {
        params: {
          'id': row.id
        }
      })
      console.log(res)
      this.$message.success(res.msg)
      this.load()
    },
    getTime (time) {
      return time.substr(0, 10)
    }
  }
}
</script>

<style scoped lang="less">
.el-card{
  margin-top: 15px;
}
</style>



