<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/adminhome'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>房屋信息</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <span>当前楼栋: {{buildNum}}</span>
      <el-row>
        <el-col :span="2.4" style="text-align: center" v-for="index of 10" :key="index">
          <el-button @click="change(index)">{{index}}</el-button>
        </el-col>
      </el-row>
      <el-row style="margin-top: 40px">
        <el-col :span="3">
          <el-avatar shape="square" style="background-color: #f4eb31"></el-avatar>
        </el-col>
        <el-col :span="3">
          <span>已出租</span>
        </el-col>
        <el-col :span="3">
          <el-avatar shape="square" style="background-color: #219cdd"></el-avatar>
        </el-col>
        <el-col :span="3">
          <span>已出售</span>
        </el-col>
      </el-row>
      <el-table
        :data="receive.data"
        border
        :row-class-name="tableRowClassName"
        fit>
        <el-table-column label="楼栋" prop="buildingId" align="center" width="100px"></el-table-column>
        <el-table-column label="楼层" prop="floor" align="center"></el-table-column>
        <el-table-column label="房间号" prop="number" align="center"></el-table-column>
        <el-table-column label="户主姓名" prop="name" align="center"></el-table-column>
        <el-table-column label="预留电话" prop="userEntity.phoneNumber" align="center"></el-table-column>
        <el-table-column label="状态" align="center">
          <template slot-scope="scope">
            <span>{{getState(scope.row.state)}}</span>
          </template>
        </el-table-column>
      </el-table>

    </el-card>
  </div>
</template>

<script>
export default {
  name: 'House1',
  data () {
    return {
      receive: {
        data: [
          {
            id: '1',
            name: 'zzz',
            buildingId: '1',
            floor: '1',
            number: '1',
            state: '0'
          },
          {
            id: '2',
            name: 'www',
            buildingId: '1',
            floor: '1',
            number: '2',
            state: '1'
          }
        ],
        size: 2
      },
      buildNum: 1,
      baseUrl: ''
    }
  },
  created () {
    this.baseUrl = this.$baseUrl
    this.load()
  },
  methods: {
    async load () {
      const {data: res} = await this.$http.get('/property_management/house/list', {params: {'buildId': this.buildNum}})
      this.receive.data = res.data
      this.receive.size = res.size
      // console.log(res)
    },
    // 弹出框关闭处理
    handleClose (done) {
      done()
    },
    openDialog () {
      this.dialogVisible = true
      this.flag = true
    },
    async change (build) {
      console.log(build)
      this.buildNum = build
      this.load()
    },
    pic (row, col) {
      if (col === 1 || col === 12) {
        return this.baseUrl + '/image/touming.png'
      }
      var floorNum = this.floor(col)
      // console.log('[' + floorNum + ',' + row + ']')
      for (var i = 0; i < this.receive.size; i++) {
        if (floorNum === this.receive.data[i].floor && row === this.receive.data[i].number) {
          // console.log('状态为')
          // console.log(this.receive.data[i].state)
          if (this.receive.data[i].state === 0) {
            // console.log('黑')
            return this.baseUrl + '/image/room.png'
          } else if (this.receive.data[i].state === 1) {
            // console.log('黄')
            return this.baseUrl + '/image/room1.png'
          } else {
            // console.log('蓝')
            return this.baseUrl + '/image/room2.png'
          }
        }
      }
    },
    floor (col) {
      if (col === 1) {
        return '楼层：'
      }
      if (col === 12) {
        return ' '
      }
      return col - 1
    },
    num (row, col) {
      if (col === 1 || col === 12) {
        return ' '
      }
      return row
    },
    getState (state) {
      if (state === 0) {
        return '未出售'
      } else if (state === 1) {
        return '已出租'
      } else {
        return '已出售'
      }
    },
    tableRowClassName (row, rowIndex) {
      // console.log('zzz')
      // console.log(row.row.state)
      if (row.row.state === 0) {
        return 'empty-row'
      } else if (row.row.state === 1) {
        return 'rent-row'
      } else if (row.row.state === 2) {
        return 'sell-row'
      }
    }
  }
}
</script>

<style scoped lang="less">
.el-card{
  margin-top: 15px;
}
#addUser{
  margin-bottom: 10px;
}
</style>



