<template>
  <el-container class="home-container">
    <!--   头部区域   -->
    <el-header>
      <div>
        <img src="src/assets/heima.png" alt="">
        <span><h3 style="color: #4a5064">智慧物业管理系统</h3></span>
      </div>

      <el-button type="info" @click="logout" style="background-color: #d01355">
        退出
      </el-button>
    </el-header>
    <!--  页面主体区域  -->
    <el-container>
      <!--   侧边栏   -->
      <el-aside :width="isCollapsed ? '64px' : '200px'">
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <!--    侧边栏菜单区域    -->
        <el-menu
            background-color="#2f70bb"
            text-color="#fff"
            active-text-color="#f8da54"
            :unique-opened="true"
            :collapse="isCollapsed"
            :collapse-transition="false"
            :router="true"
            :default-active="activePath" >
          <!--  一级菜单  -->
          <el-submenu :index="item.id+''" v-for="item in menulist" :key="item.id">
            <!-- 一级菜单模板区域 -->
            <template slot="title">
              <!-- 图标 -->
              <i :class="iconsObj[item.id]"></i>
              <!-- 文本 -->
              <span>{{item.authName}}</span>
            </template>
            <!-- 二级菜单 -->
            <el-menu-item
                :index="subItem.path"
                v-for="subItem in item.childrens"
                :key="subItem.id"
                @click="saveNavState(subItem.path)">
              <template slot="title">
                <!-- 图标 -->
                <i class="el-icon-menu"></i>
                <!-- 文本 -->
                <span>{{ subItem.authName }}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!--   右侧内容主体   -->
      <el-main>
        <!--    路由占位符    -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: 'AdminHome',
  data () {
    return {
      // 左侧菜单数据
      menulist: [
        {
          id: 101,
          authName: '管理',
          path: null,
          childrens: [
            {
              id: 1010,
              authName: '用户添加',
              path: '/adminuser'
            },
            {
              id: 1011,
              authName: '房屋信息查询',
              path: '/adminhouse1'
            },
            {
              id: 1012,
              authName: '发布公告',
              path: '/adminannouncement'
            },
            {
              id: 1013,
              authName: '报修申请',
              path: '/adminrepair'
            },
            {
              id: 1014,
              authName: '返乡信息',
              path: '/adminreturn'
            },
            {
              id: 1015,
              authName: '投诉处理',
              path: '/admincomplaints'
            }
          ]
        }
      ],
      iconsObj: {
        125: 'iconfont icon-user',
        103: 'el-icon-document',
        102: 'el-icon-question',
        101: 'el-icon-reading',
        145: 'iconfont icon-baobiao'
      },
      // 是否折叠
      isCollapsed: false,
      // 被激活的链接地址
      activePath: ''
    }
  },
  created () {
    this.activePath = window.sessionStorage.getItem('activePath')
  },
  methods: {
    logout () {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },

    // 点击按钮，切换菜单折叠与展开
    toggleCollapse () {
      this.isCollapsed = !this.isCollapsed
    },
    // 保存链接的激活状态
    saveNavState (activePath) {
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
    }
  }
}
</script>

<style lang="less" scoped>
.el-header {
  background-color: #f7f9f9;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #ffffff;
  font-size: 20px;
  > div{
    display: flex;
    align-items: center;
    span {
      margin-left: 15px;
    }
  }
}
.el-aside {
  background-color: #2f70bb;
  .el-menu {
    border-right: 0px;
  }
}
.el-main {
  background-color: #eaedf1;
  height: 100%;
}
.home-container {
  position: absolute;
  height: 100%;
  width: 100%;
}
.iconfont {
  margin-right: 10px;
}
.toggle-button{
  background-color: #2f70bb;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
</style>



