<template>
  <div class="login_container">
    <div class="title">
      <p>智慧物业管理系统</p>
    </div>
    <div class="login_box">
      <!--头像区域-->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt="">
      </div>
      <!--登陆表单区域-->
      <el-form ref="loginFormRef" :model="loginForm" label-width="0px" class="login_form">
        <!--用户名-->
        <el-form-item prop="username">
          <el-input v-model="loginForm.sno" prefix-icon="el-icon-user" placeholder="账号/手机号"></el-input>
        </el-form-item>
        <!--密码-->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="el-icon-lock"
                    type="password" :show-password="true" placeholder="密码"></el-input>
        </el-form-item>

        <!--按钮区-->
        <el-form-item class="btns">
          <el-select class="select" v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-button type="primary" @click="login">登录</el-button>

        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      radio: 1,
      // 这是登陆表单的数据绑定对象
      loginForm: {
        sno: '',
        password: ''
      },
      options: [
        {
          value: 1,
          label: '用户'
        },
        {
          value: 2,
          label: '管理员'
        }
      ],
      value: '',
      query: {
        sno: '',
        password: '',
        repassword: '',
        name: '',
        email: ''
      },
      receive: {}
    }
  },
  methods: {
    // 点击重置按钮，重置登录表单
    openDialog () {
      // console.log(this)
      this.dialogFormVisible = true
      // this.query.status=this.value
      console.log(this.query.status)
    },
    //
    async login () {
      if (this.value === 1) {
        const { data: res } = await this.$http.get('/property_management/user/login', {
          params: {
            'phoneNumber': this.loginForm.sno,
            'password': this.loginForm.password
          }
        })

        this.receive = res.data
        console.log(res)

        if (res.code === 200) {
          window.sessionStorage.setItem('id', this.receive.id)
          window.sessionStorage.setItem('phoneNumber', this.receive.phoneNumber)
          window.sessionStorage.setItem('name', this.receive.name)
          window.sessionStorage.setItem('houseId', this.receive.houseId)
          window.sessionStorage.setItem('email', this.receive.email)
          window.sessionStorage.setItem('park', this.receive.parking_number)
          window.sessionStorage.setItem('buildingId', this.receive.houseEntity.buildingId)
          window.sessionStorage.setItem('floor', this.receive.houseEntity.floor)
          window.sessionStorage.setItem('number', this.receive.houseEntity.number)
          window.sessionStorage.setItem('state', this.receive.houseEntity.state)
          window.sessionStorage.setItem('parkingTime', this.receive.parkingTime)

          this.$message.success('登陆成功！')
          this.$router.push('/userhome')
        } else {
          this.$message.error('登陆失败！')
        }
      } else {
        this.loginForm.username = this.loginForm.sno
        const { data: res } = await this.$http.get('/property_management/admin/login', { params: this.loginForm })

        this.receive = res.data
        console.log(res)

        if (res.code === 200) {
          window.sessionStorage.setItem('id', this.receive.id)
          window.sessionStorage.setItem('username', this.receive.username)
          window.sessionStorage.setItem('adminFlag', '1')

          this.$message.success('登陆成功！')
          this.$router.push('/adminhome')
        } else {
          this.$message.error('登陆失败！')
        }
      }
    }
  }
}
</script>

<!--需手动安装less和lessloader :npm install less@3.9.0
                              npm insatll less-loader@4.1.0
                              ui界面安装版本过高会报错-->
<style lang="less" scoped>
.login_container {
  background-color: #F7F7F7;
  position: absolute;
  height: 100%;
  width: 100%;
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  .avatar_box {
    height: 80px;
    width: 80px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #eee;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}

.btns {
  display: flex;
  justify-content: flex-end;
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.select {
  //margin-right: 38px;

  width: 50%;
  margin-left: 20px;
  margin-right: 20px;
  float: left;

}

.title {
  font-size: 50px;
  Font-Family: "华文行楷";
  font-weight: bold;
  text-align: center;
  color: #000000;
  display: inline
}
</style>



