<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/userhome'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>投诉处理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>

      <el-col :span="8">
        <el-button type="primary" @click="openDialog" id="addUser" style="margin-bottom: 20px">投诉提交</el-button>
      </el-col>

      <el-dialog
        width="650px"
        :before-close="handleClose"
        :visible.sync="dialogVisible">

        <el-form ref="form" :model="form" label-width="300px" style="width: 100%">
          <el-form-item label="房间id" label-width="100px">
            <el-input v-model="userMsg.houseId" style="width: 500px" @blur="judge" disabled></el-input>
          </el-form-item>

          <el-form-item label="房间号" label-width="100px">
            <span><span>{{userMsg.buildingId}} 栋 {{userMsg.floor}} 层 {{userMsg.number}} 号 </span></span>
          </el-form-item>

          <el-form-item label="姓名" label-width="100px">
            <el-input v-model="userMsg.name" style="width: 500px" @blur="judge" disabled></el-input>
          </el-form-item>

          <el-form-item label="手机号" label-width="100px">
            <el-input v-model="userMsg.phoneNumber" style="width: 500px" @blur="judge" disabled></el-input>
          </el-form-item>

          <el-form-item label="邮箱" label-width="100px">
            <el-input v-model="userMsg.email" style="width: 500px" @blur="judge" disabled></el-input>
          </el-form-item>
          <el-form-item label="投诉内容" label-width="100px">
            <el-input v-model="form.content" style="width: 500px" @blur="judge" type="textarea"></el-input>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="submit" :disabled="flag" >确认提交</el-button>
        </div>
      </el-dialog>

      <el-dialog
        width="650px"
        :before-close="handleClose"
        :visible.sync="dialogErrorVisible">
        <el-form ref="form" :model="form" label-width="300px" style="width: 100%">
          <el-form-item label="投诉内容包含违禁词，请重新输入" label-width="300px">
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="cancel" :disabled="flag" >确认</el-button>
        </div>
      </el-dialog>

      <el-table
        :data="this.receive"
        style="width: 100%"
        :row-class-name="tableRowClassName"
        border
        fit>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row>
              <el-col :span="2">
                <span style="text-align: center"><h1>投诉内容:</h1></span>
              </el-col>
              <el-col :span="19">
                <el-input v-model="scope.row.content" type="textarea" disabled></el-input>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column label="投诉编号" prop="id" align="center">

        </el-table-column>
        <el-table-column label="房间号" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.userEntity.houseEntity.buildingId}} 栋 {{scope.row.userEntity.houseEntity.floor}} 层 {{scope.row.userEntity.houseEntity.number}} 号 </span>
          </template>
        </el-table-column>
        <el-table-column label="户主姓名" prop="userEntity.name" align="center">

        </el-table-column>
        <el-table-column label="联系电话" prop="userEntity.phoneNumber" align="center">

        </el-table-column>
        <el-table-column label="提交时间" align="center">
          <template slot-scope="scope">
            <span>{{getTime(scope.row.time)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="投诉状态" prop="state" align="center">
          <template slot-scope="scope">
            <span>{{getState(scope.row.state)}}</span>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Complaints',
  data () {
    return {
      receive: [],
      form: {
        userId: '',
        houseId: '',
        content: ''
      },
      userMsg: {

      },
      dialogVisible: false,
      dialogErrorVisible: false,
      flag: true
    }
  },
  created () {
    this.userMsg.id = window.sessionStorage.getItem('id')
    this.userMsg.name = window.sessionStorage.getItem('name')
    this.userMsg.buildingId = window.sessionStorage.getItem('buildingId')
    this.userMsg.floor = window.sessionStorage.getItem('floor')
    this.userMsg.number = window.sessionStorage.getItem('number')
    this.userMsg.state = window.sessionStorage.getItem('state')
    // this.form.parkingTime = window.sessionStorage.getItem('parkingTime')
    this.userMsg.email = window.sessionStorage.getItem('email')
    this.userMsg.houseId = window.sessionStorage.getItem('houseId')
    this.userMsg.phoneNumber = window.sessionStorage.getItem('phoneNumber')
    this.load()
  },
  methods: {
    async load () {
      const {data: res} = await this.$http.get('/property_management/complaints/listbyid', {params: {'uid': this.userMsg.id}})
      console.log(res)
      this.receive = res.data
    },
    async submit () {
      const {data: res} = await this.$http.get('/property_management/complaints/submit', {params: this.form})
      console.log(res)
      if (res.code === 200) {
        this.$message.success(res.msg)
        this.dialogVisible = false
        await this.load()
      } else {
        this.$message.error(res.msg)
        this.dialogVisible = false
        this.dialogErrorVisible = true
        await this.load()
      }
    },
    cancel () {
      this.dialogVisible = true
      this.dialogErrorVisible = false
    },
    tableRowClassName (row, rowIndex) {
      if (row.row.state === 0) {
        return 'warning-row'
      } else if (row.row.state === 1) {
        return 'danger-row'
      } else if (row.row.state === 2) {
        return 'success-row'
      }
    },
    getState (state) {
      if (state === 0) {
        return '未接收'
      } else if (state === 1) {
        return '处理中'
      } else if (state === 2) {
        return '已处理'
      }
    },
    async receiveComplaint (row) {
      const {data: res} = await this.$http.get('/property_management/complaints/receive', {
        params: {
          'id': row.id
        }
      })
      console.log(res)
      this.$message.success(res.msg)
      this.load()
    },
    async finishComplaint (row) {
      const {data: res} = await this.$http.get('/property_management/complaints/finish', {
        params: {
          'id': row.id
        }
      })
      console.log(res)
      this.$message.success(res.msg)
      this.load()
    },
    getTime (time) {
      return time.substr(0, 10)
    },
    // 弹出框关闭处理
    handleClose (done) {
      done()
    },
    openDialog () {
      this.form.userId = this.userMsg.id
      this.form.houseId = this.userMsg.houseId
      this.dialogVisible = true
      this.flag = true
    },
    judge () {
      if (this.form.content === '') {
        this.flag = true
        return
      }
      this.flag = false
      console.log(5)
    }
  }
}
</script>

<style scoped lang="less">
.el-card{
  margin-top: 15px;
}
</style>



