<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/adminhome'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>房屋信息</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <span>当前楼栋: {{buildNum}}</span>
      <el-row>
        <el-col :span="2.4" style="text-align: center" v-for="index of 10" :key="index">
          <el-button @click="change(index)">{{index}}</el-button>
        </el-col>
      </el-row>

      <el-row style="margin-top: 20px">
        <el-col :span="2" style="text-align: center" v-for="j of 12" :key="j">
          <span>{{floor(j)}}</span>
        </el-col>
      </el-row>
      <el-row v-for="i of 5" style="margin-top: 20px" :key="i">
        <el-col :span="2"  style="text-align: center" v-for="j of 12" :key="j">
          <span>{{num(i,j)}}</span>
          <el-avatar shape="square" style="background-color: transparent;padding-bottom: 20px">
            <el-image :src="pic(i,j)"></el-image>
          </el-avatar>

        </el-col>
      </el-row>

      <el-row style="margin-top: 40px">
        <el-col :span="3">
          <el-avatar shape="square" style="background-color: #010101"></el-avatar>
        </el-col>
        <el-col :span="3">
          <span>未出售</span>
        </el-col>
        <el-col :span="3">
          <el-avatar shape="square" style="background-color: #f4eb31"></el-avatar>
        </el-col>
        <el-col :span="3">
          <span>已出租</span>
        </el-col>
        <el-col :span="3">
          <el-avatar shape="square" style="background-color: #219cdd"></el-avatar>
        </el-col>
        <el-col :span="3">
          <span>已出售</span>
        </el-col>
      </el-row>

    </el-card>
  </div>
</template>

<script>
export default {
  name: 'House',
  data () {
    return {
      receive: {
        data: [
          {
            id: '1',
            name: 'zzz',
            buildingId: '1',
            floor: '1',
            number: '1',
            state: '0'
          },
          {
            id: '2',
            name: 'www',
            buildingId: '1',
            floor: '1',
            number: '2',
            state: '1'
          }
        ],
        size: 2
      },
      buildNum: 1,
      baseUrl: ''
    }
  },
  created () {
    this.baseUrl = this.$baseUrl
    this.load()
  },
  methods: {
    async load () {
      const {data: res} = await this.$http.get('/property_management/house/list', {params: {'buildId': this.buildNum}})
      this.receive.data = res.data
      this.receive.size = res.size
      console.log(res)
    },
    // 弹出框关闭处理
    handleClose (done) {
      done()
    },
    openDialog () {
      this.dialogVisible = true
      this.flag = true
    },
    async change (build) {
      console.log(build)
      this.buildNum = build
      await this.load()
    },
    pic (row, col) {
      if (col === 1 || col === 12) {
        return this.baseUrl + '/image/touming.png'
      }
      var floorNum = this.floor(col)
      // console.log('[' + floorNum + ',' + row + ']')
      for (var i = 0; i < this.receive.size; i++) {
        if (floorNum === this.receive.data[i].floor && row === this.receive.data[i].number) {
          // console.log('状态为')
          // console.log(this.receive.data[i].state)
          if (this.receive.data[i].state === 0) {
            // console.log('黑')
            return this.baseUrl + '/image/room.png'
          } else if (this.receive.data[i].state === 1) {
            // console.log('黄')
            return this.baseUrl + '/image/room1.png'
          } else {
            // console.log('蓝')
            return this.baseUrl + '/image/room2.png'
          }
        }
      }
    },
    floor (col) {
      if (col === 1) {
        return '楼层：'
      }
      if (col === 12) {
        return ' '
      }
      return col - 1
    },
    num (row, col) {
      if (col === 1 || col === 12) {
        return ' '
      }
      return row
    }
  }
}
</script>

<style scoped lang="less">
.el-card{
  margin-top: 15px;
}
#addUser{
  margin-bottom: 10px;
}
</style>



