<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/userhome'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>返乡登记</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-col :span="8">
        <el-button type="primary" @click="openDialog" id="addUser" style="margin-bottom: 20px">返乡登记提交</el-button>
      </el-col>

      <el-dialog
        width="650px"
        :before-close="handleClose"
        :visible.sync="dialogVisible">

        <el-form ref="form" :model="form" label-width="300px" style="width: 100%">
          <el-form-item label="房间id" label-width="100px">
            <el-input v-model="userMsg.houseId" style="width: 500px" @blur="judge" disabled></el-input>
          </el-form-item>

          <el-form-item label="房间号" label-width="100px">
            <span><span>{{userMsg.buildingId}} 栋 {{userMsg.floor}} 层 {{userMsg.number}} 号 </span></span>
          </el-form-item>

          <el-form-item label="姓名" label-width="100px">
            <el-input v-model="form.name" style="width: 500px" @blur="judge"></el-input>
          </el-form-item>

          <el-form-item label="地区选择" label-width="100px">
            <v-distpicker @province="province" @city="city" @area="area" @selected="judge"></v-distpicker>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="submit" :disabled="flag" >确认提交</el-button>
        </div>
      </el-dialog>
      <el-table
        :data="this.receive"
        style="width: 100%"
        :row-class-name="tableRowClassName"
        border
        fit>
        <el-table-column label="返乡登记编号" prop="id" align="center">

        </el-table-column>
        <el-table-column label="房间号" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.houseEntity.buildingId}} 栋 {{scope.row.houseEntity.floor}} 层 {{scope.row.houseEntity.number}} 号 </span>
          </template>
        </el-table-column>
        <el-table-column label="姓名" prop="name" align="center">

        </el-table-column>
        <el-table-column label="省份" prop="province" align="center">

        </el-table-column>
        <el-table-column label="城市" prop="city" align="center">

        </el-table-column>
        <el-table-column label="区" prop="area" align="center">

        </el-table-column>
        <el-table-column label="时间" align="center">
          <template slot-scope="scope">
            <span>{{getTime(scope.row.time)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center" width="100px">
          <template slot-scope="scope">
            <el-button-group>
              <el-tooltip class="item" effect="dark" content="撤回" placement="top-end">
                <el-button type="danger" @click="withdraw(scope.row)" icon="el-icon-delete" ></el-button>
              </el-tooltip>
            </el-button-group>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Return',
  data () {
    return {
      receive: [],
      form: {
        userId: '',
        houseId: '',
        name: '',
        province: '',
        city: '',
        area: ''
      },
      userMsg: {

      },
      dialogVisible: false,
      flag: true,
      select: {
        province: '广东省',
        city: '广州市',
        area: '海珠区'
      }
    }
  },
  created () {
    this.userMsg.id = window.sessionStorage.getItem('id')
    this.userMsg.name = window.sessionStorage.getItem('name')
    this.userMsg.buildingId = window.sessionStorage.getItem('buildingId')
    this.userMsg.floor = window.sessionStorage.getItem('floor')
    this.userMsg.number = window.sessionStorage.getItem('number')
    this.userMsg.state = window.sessionStorage.getItem('state')
    // this.form.parkingTime = window.sessionStorage.getItem('parkingTime')
    this.userMsg.email = window.sessionStorage.getItem('email')
    this.userMsg.houseId = window.sessionStorage.getItem('houseId')
    this.userMsg.phoneNumber = window.sessionStorage.getItem('phoneNumber')
    this.load()
  },
  methods: {
    async load () {
      const {data: res} = await this.$http.get('/property_management/returnstatistical/listbyid', {params: {'uid': this.userMsg.id}})
      this.receive = res.data
      console.log(this.receive)
    },
    // 弹出框关闭处理
    handleClose (done) {
      done()
    },
    openDialog () {
      this.form.userId = this.userMsg.id
      this.form.houseId = this.userMsg.houseId
      this.dialogVisible = true
      this.flag = true
    },
    judge () {
      console.log(this.form)
      console.log(this.select)
      if (this.form.name === '') {
        this.flag = true
        return
      } else if (this.form.province === '') {
        this.flag = true
        return
      } else if (this.form.city === '') {
        this.flag = true
        return
      } else if (this.form.area === '') {
        this.flag = true
        return
      }
      this.flag = false
      console.log(5)
    },
    tableRowClassName (row, rowIndex) {
      if (row.row.state === 0) {
        return 'warning-row'
      } else if (row.row.state === 1) {
        return 'success-row'
      }
    },
    getState (state) {
      if (state === 0) {
        return '未维修'
      } else {
        return '已维修'
      }
    },
    province (province) {
      this.form.province = province.value
    },
    city (city) {
      this.form.city = city.value
    },
    area (area) {
      this.form.area = area.value
    },
    async submit () {
      const {data: res} = await this.$http.get('/property_management/returnstatistical/submit', {params: this.form})
      console.log(res)
      this.$message.success(res.msg)
      this.dialogVisible = false
      this.load()
    },
    getTime (time) {
      console.log('111')
      console.log(this.form.propertyTime)
      return time.substr(0, 10)
    },
    async withdraw (row) {
      const {data: res} = await this.$http('/property_management/returnstatistical/withdraw', {params: {'id': row.id}})
      console.log(res)
      this.$message.success(res.msg)
      this.load()
    }
  }
}
</script>

<style scoped lang="less">
.el-card{
  margin-top: 15px;
}
</style>



