<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/adminhome'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>发布公告</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-col :span="8">
        <el-button type="primary" @click="openDialog" style="margin-bottom: 20px">发布新公告</el-button>
      </el-col>

      <el-dialog
        width="70%"
        :before-close="handleClose"
        :visible.sync="dialogVisible">

        <el-form ref="form" :model="form" label-width="300px" style="width: 100%">
          <el-form-item label="标题" label-width="100px">
            <el-input v-model="form.title" style="width: 500px" @blur="judge"></el-input>
          </el-form-item>

          <el-form-item label="公告正文" label-width="100px">
            <el-input v-model="form.content" type="textarea" @blur="judge" :row="500" style="width: 100%;height: 300px"></el-input>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="release" :disabled="flag" >发布</el-button>
        </div>
      </el-dialog>

      <el-table
        :data="receive.data"
        border
        stripe
        fit>

        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-card>
              <span>{{scope.row.content}}</span><br><br><br>
            </el-card>
          </template>
        </el-table-column>

        <el-table-column label="id" prop="id" align="center" width="100px"></el-table-column>
        <el-table-column label="标题" prop="title" align="center"></el-table-column>
        <el-table-column label="发布时间" align="center" width="250px">
          <template slot-scope="scope">
            <span>{{getTime(scope.row.time)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center" width="100px">
          <template slot-scope="scope">
            <el-button-group>
              <el-tooltip class="item" effect="dark" content="撤回公告" placement="top-end">
                <el-button type="danger" @click="withdraw(scope.row)" icon="el-icon-delete" ></el-button>
              </el-tooltip>
            </el-button-group>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Announcement',
  data () {
    return {
      receive: {
        data: [
          {
            id: '1',
            title: '第一条公告',
            content: '第一条公告内容',
            time: ''
          },
          {
            id: '2',
            title: '第二条公告',
            content: '第二条公告内容',
            time: ''
          }
        ]
      },
      dialogVisible: false,
      form: {
        title: '',
        content: ''
      },
      flag: true,
      myRemark: ''
    }
  },
  created () {
    this.load()
  },
  methods: {
    async load () {
      const {data: res} = await this.$http.get('/property_management/announcement/list')
      this.receive.data = res.data
      console.log(res)
    },
    // 弹出框关闭处理
    handleClose (done) {
      done()
    },
    openDialog () {
      this.dialogVisible = true
      this.flag = true
    },
    async release () {
      const {data: res} = await this.$http.get('/property_management/announcement/submit', {params: this.form})

      console.log(res)

      if (res.code === 200) {
        this.$message.success('公告发布成功！')
        this.dialogVisible = false
        this.load()
      } else {
        this.$message.error('公告发布失败！')
      }
    },
    judge () {
      if (this.form.title === '') {
        this.flag = true
        console.log(1)
        return
      }
      if (this.form.content === '') {
        this.flag = true
        console.log(2)
        return
      }
      this.flag = false
      console.log(3)
    },
    getTime (time) {
      time = time + ''
      return time.substr(0, 10)
    },
    async withdraw (row) {
      const {data: res} = await this.$http('/property_management/announcement/withdraw', {params: {'id': row.id}})
      console.log(res)
      this.$message.success(res.msg)
      await this.load()
    }
  }
}
</script>

<style scoped lang="less">
.el-card{
  margin-top: 15px;
}
</style>



