<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/adminhome'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户添加</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-col :span="8">
        <el-button type="primary" @click="openDialog" id="addUser">添加用户</el-button>
      </el-col>

      <el-dialog
        width="650px"
        :before-close="handleClose"
        :visible.sync="dialogVisible">

        <el-form ref="form" :model="form" label-width="300px" style="width: 100%">
          <el-form-item label="房间号" label-width="100px">
            <el-row>
              <el-col :span="4" style="padding-left: 20px">
                <el-input v-model="form.buildingId"></el-input>
              </el-col>
              <el-col :span="2" style="padding-left: 10px">
                <span>栋</span>
              </el-col>
              <el-col :span="4" style="padding-left: 20px">
                <el-input v-model="form.floor"></el-input>
              </el-col>
              <el-col :span="2" style="padding-left: 10px">
                <span>层</span>
              </el-col>
              <el-col :span="4" style="padding-left: 20px">
                <el-input v-model="form.number"></el-input>
              </el-col>
              <el-col :span="2" style="padding-left: 10px">
                <span>号</span>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="合同形式" label-width="100px">
            <el-select v-model="form.state" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="户主姓名" label-width="100px">
            <el-input v-model="form.name" style="width: 500px" @blur="judge"></el-input>
          </el-form-item>

          <el-form-item label="预留手机号" label-width="100px">
            <el-input v-model="form.phoneNumber" style="width: 500px" @blur="judge"></el-input>
          </el-form-item>

          <el-form-item label="密码" label-width="100px" >
            <el-input v-model="form.password" style="width: 500px" @blur="judge" show-password></el-input>
          </el-form-item>

          <el-form-item label="邮箱" label-width="100px">
            <el-input v-model="form.email" style="width: 500px" @blur="judge"></el-input>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="add" :disabled="flag" >确认添加</el-button>
        </div>
      </el-dialog>

      <el-table
        ref="Table"
        :data="receive.data"
        border
        stripe
        fit>
        <el-table-column type="index" width="50" align="center"></el-table-column>
        <el-table-column label="id" prop="id" align="center" ></el-table-column>
        <el-table-column label="户主姓名" prop="name" align="center" ></el-table-column>
        <el-table-column label="电话号码" prop="phoneNumber" align="center" ></el-table-column>
        <el-table-column label="邮箱" prop="email" align="center" ></el-table-column>
        <el-table-column label="楼栋" prop="houseEntity.buildingId" align="center" ></el-table-column>
        <el-table-column label="楼层" prop="houseEntity.floor" align="center" ></el-table-column>
        <el-table-column label="房间号" prop="houseEntity.number" align="center" ></el-table-column>
        <el-table-column label="操作" width="160" align="center">
          <template slot-scope="scope">
            <el-button size="mini" type="danger" @click="del(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'User',
  data () {
    return {
      receive: {
        data: [
          {
            id: '1',
            phoneNumber: '123456',
            name: 'qqq',
            email: '131',
            parkingNumber: ''
          },
          {
            id: '1',
            phoneNumber: '223456',
            name: 'www',
            email: '131',
            parkingNumber: ''
          }
        ]
      },
      dialogVisible: false,
      form: {
        phoneNumber: '',
        name: '',
        password: '',
        email: '',
        buildingId: '',
        floor: '',
        number: '',
        state: ''
      },
      flag: true,
      myRemark: '',
      options: [{
        value: 1,
        label: '出租'
      }, {
        value: 2,
        label: '出售'
      }]
    }
  },
  created () {
    this.load()
  },
  methods: {
    async load () {
      const {data: res} = await this.$http.get('/property_management/user/list')
      console.log(res)
      this.receive.data = res.data
    },
    // 弹出框关闭处理
    handleClose (done) {
      done()
    },
    openDialog () {
      this.dialogVisible = true
      this.flag = true
    },
    async add () {
      console.log(this.form)
      const {data: res} = await this.$http.get('/property_management/user/adduser', {params: this.form})
      console.log(res)
      if (res.code === 200) {
        this.$message.success(res.msg)
        this.dialogVisible = false
        await this.load()
      } else {
        this.$message.error(res.msg)
      }
    },
    async del (row) {
      const { data: res } = await this.$http.get('/property_management/user/deleteUser',
        { params: { 'id': row.id,
          'buildingId': row.houseEntity.buildingId,
          'floor': row.houseEntity.floor,
          'number': row.houseEntity.number
        } })
      console.log(res)
      if (res.code === 200) {
        this.$message.success(res.msg)
        await this.load()
      } else {
        this.$message.error(res.msg)
      }
      await this.load()
    },
    judge () {
      if (this.form.name === '') {
        this.flag = true
        console.log(1)
        return
      }
      if (this.form.phoneNumber === '') {
        this.flag = true
        console.log(2)
        return
      }
      if (this.form.email === '') {
        this.flag = true
        console.log(3)
        return
      }
      if (this.form.password === '') {
        this.flag = true
        console.log(4)
        return
      }
      this.flag = false
      console.log(5)
    }
  }
}
</script>

<style scoped lang="less">
.el-card{
  margin-top: 15px;
}
#addUser{
  margin-bottom: 10px;
}
</style>



