<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/userhome'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>物业费缴纳</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-form ref="form" :model="form" label-width="100px">
        <el-form-item label="户主姓名">
          <span>{{form.name}}</span>
        </el-form-item>
        <el-form-item label="房间号">
          <span>{{form.buildingId}} 栋 {{form.floor}} 层 {{form.number}} 号 </span>
        </el-form-item>
        <el-form-item label="当前余额">
          <span>{{form.balance}}</span>
        </el-form-item>
        <el-form-item label="物业费有效期">
          <span>{{getTime(form.propertyTime)}}</span>
        </el-form-item>
        <el-form-item label="缴费时长(月)">
          <el-row>
            <el-col :span="12">
              <el-radio-group v-model="radio">
                <el-radio label="1">1个月</el-radio>
                <el-radio label="2">2个月</el-radio>
                <el-radio label="3">3个月</el-radio>
                <el-radio label="6">6个月</el-radio>
                <el-radio label="12">12个月</el-radio>
              </el-radio-group>
            </el-col>
            <el-col :span="6">
              <span>总价格： {{calMoney()}}</span>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="验证码">
          <el-row>
            <el-col :span="12">
              <el-input v-model="form.proofcode" placeholder="邮箱验证码"></el-input>
            </el-col>
            <el-col :span="4" style="margin-left: 20px">
              <el-button type="primary" @click="sendProofCode">发送邮箱验证码</el-button>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">确认缴费</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Fee',
  data () {
    return {
      value1: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
      radio: '1',
      form: {
        name: '',
        buildingId: '',
        floor: '',
        number: '',
        state: '',
        parkingTime: '',
        proofcode: '',
        propertyTime: '',
        balance: ''
      },
      recieve: {
        msg: ''
      }

    }
  },
  created () {
    this.form.id = window.sessionStorage.getItem('id')
    this.form.name = window.sessionStorage.getItem('name')
    this.form.buildingId = window.sessionStorage.getItem('buildingId')
    this.form.floor = window.sessionStorage.getItem('floor')
    this.form.number = window.sessionStorage.getItem('number')
    this.form.state = window.sessionStorage.getItem('state')
    // this.form.parkingTime = window.sessionStorage.getItem('parkingTime')
    this.form.email = window.sessionStorage.getItem('email')
    this.load()
  },
  methods: {
    async load () {
      const {data: res} = await this.$http.get('/property_management/user/getpropertytime', {params: {'id': this.form.id}})
      console.log(res)
      this.form.parkingTime = res.data.parkingTime
      this.form.propertyTime = res.data.propertyTime
      this.form.balance = res.data.balance
    },
    async onSubmit () {
      console.log('submit!')
      if (this.form.proofcode === '') {
        this.$message.error('请输入验证码！')
        return
      }
      const {data: res} = await this.$http.get('/property_management/user/checkpropertycode', {params: {'id': this.form.id, 'propertyCode': this.form.proofcode, 'month': this.radio}})
      console.log(res)
      if (res.code === 200) {
        this.$message.success(res.msg)
      } else {
        this.$message.error(res.msg)
      }
      this.load()
      this.form.proofcode = ''
    },
    getTime (time) {
      console.log('111')
      console.log(this.form.propertyTime)
      return time.substr(0, 10)
    },
    async sendProofCode () {
      const {data: res} = await this.$http.get('/property_management/user/sendpropertycode', {params: {'email': this.form.email, 'id': this.form.id}})
      // this.recieve.msg = res.data.msg
      console.log(res)
      this.$message.success('发送成功！')
    },
    calMoney () {
      return this.radio * 50
    }
  }
}
</script>

<style scoped lang="less">
.el-card{
  margin-top: 15px;
}
</style>



