<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/adminhome'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>报修登记</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-table
        :data="this.receive"
        style="width: 100%"
        :row-class-name="tableRowClassName"
        border
        fit>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row>
              <el-col :span="2">
                <span style="text-align: center"><h1>报修内容:</h1></span>
              </el-col>
              <el-col :span="19">
                <el-input v-model="scope.row.content" type="textarea" disabled></el-input>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column label="报修编号" prop="id" align="center">

        </el-table-column>
        <el-table-column label="房间号" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.userEntity.houseEntity.buildingId}} 栋 {{scope.row.userEntity.houseEntity.floor}} 层 {{scope.row.userEntity.houseEntity.number}} 号 </span>
          </template>
        </el-table-column>
        <el-table-column label="户主姓名" prop="userEntity.name" align="center">

        </el-table-column>
        <el-table-column label="提交时间" align="center">
          <template slot-scope="scope">
            <span>{{getTime(scope.row.time)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="维修状态" prop="state" align="center">
          <template slot-scope="scope">
            <span>{{getState(scope.row.state)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button-group>
              <el-tooltip class="item" effect="dark" content="确认已维修" placement="top-end">
                <el-button type="primary" @click="checkRepair(scope.row)" icon="el-icon-check" :disabled="judgeBtn(scope.row.state)"></el-button>
              </el-tooltip>
            </el-button-group>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Repair',
  data () {
    return {
      receive: [],
      form: {
        userId: '',
        houseId: '',
        content: ''
      },
      userMsg: {

      }
    }
  },
  created () {
    this.load()
  },
  methods: {
    async load () {
      const {data: res} = await this.$http.get('/property_management/repair/list')
      console.log(res)
      this.receive = res.data
    },
    async submit () {
      const {data: res} = await this.$http.get('/property_management/repair/submit', {params: this.form})
      console.log(res)
      this.$message.success(res.msg)
    },
    tableRowClassName (row, rowIndex) {
      if (row.row.state === 0) {
        return 'warning-row'
      } else if (row.row.state === 1) {
        return 'success-row'
      }
    },
    getState (state) {
      if (state === 0) {
        return '未维修'
      } else {
        return '已维修'
      }
    },
    judgeBtn (state) {
      if (state === 0) {
        return false
      } else {
        return true
      }
    },
    async checkRepair (row) {
      const {data: res} = await this.$http.get('/property_management/repair/checkrepair', {
        params: {
          'id': row.id,
          'userId': row.userId,
          'houseId': row.houseId,
          'content': row.content,
          'state': row.state
        }
      })
      console.log(res)
      this.$message.success(res.msg)
      this.load()
    },
    getTime (time) {
      console.log('111')
      console.log(this.form.propertyTime)
      return time.substr(0, 10)
    }
  }
}
</script>

<style scoped lang="less">
.el-card{
  margin-top: 15px;
}
</style>



