<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/userhome'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>查看公告</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-table
        :data="receive.data"
        border
        stripe
        fit>

        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-card>
              <span>{{scope.row.content}}</span><br><br><br>

            </el-card>
          </template>
        </el-table-column>

        <el-table-column label="id" prop="id" align="center" width="100px"></el-table-column>
        <el-table-column label="标题" prop="title" align="center"></el-table-column>
        <el-table-column label="发布时间" align="center" width="250px">
          <template slot-scope="scope">
            <span>{{getTime(scope.row.time)}}</span>
          </template>
        </el-table-column>

      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Announcement',
  data () {
    return {
      receive: {
        data: [
          {
            id: '1',
            title: '第一条公告',
            content: '第一条公告内容'
          },
          {
            id: '2',
            title: '第二条公告',
            content: '第二条公告内容'
          }
        ]
      },
      myRemark: ''
    }
  },
  created () {
    this.load()
  },
  methods: {
    async load () {
      const {data: res} = await this.$http.get('/property_management/announcement/list')
      this.receive.data = res.data
      console.log(res)
    },
    async submitRemark (row) {
      console.log(1)
      const sid = window.sessionStorage.getItem('id')

      const {data: res} = await this.$http.get('/library_occupy/remark/submitRemark', {params: {aid: row.id, sid: sid, remark: this.myRemark}})

      console.log(res)

      if (res.code === 200) {
        this.$message.success(res.msg)
      } else {
        this.$message.error(res.msg)
      }
      this.load()
    },
    getTime (time) {
      time = time + ''
      return time.substr(0, 10)
    }
  }
}
</script>

<style scoped lang="less">
.el-card{
  margin-top: 15px;
}
</style>



