<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/userhome'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>余额充值</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-form ref="form" :model="form" label-width="100px">
        <el-form-item label="户主姓名">
          <span>{{form.name}}</span>
        </el-form-item>
        <el-form-item label="房间号">
          <span>{{form.buildingId}} 栋 {{form.floor}} 层 {{form.number}} 号 </span>
        </el-form-item>
        <el-form-item label="当前余额">
          <span>{{form.balance}}</span>
        </el-form-item>
        <el-form-item label="充值">
          <el-row>
            <el-col :span="8">
              <el-input v-model="form.money" placeholder="充值金额"></el-input>
            </el-col>
            <el-col :span="4" style="margin-left: 20px">
              <el-button type="primary" @click="recharge()">确认缴费</el-button>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Recharge',
  data () {
    return {
      form: {
        id: '',
        name: '',
        buildingId: '',
        floor: '',
        number: '',
        balance: '',
        money: ''
      }
    }
  },
  created () {
    this.form.id = window.sessionStorage.getItem('id')
    this.form.name = window.sessionStorage.getItem('name')
    this.form.buildingId = window.sessionStorage.getItem('buildingId')
    this.form.floor = window.sessionStorage.getItem('floor')
    this.form.number = window.sessionStorage.getItem('number')
    this.getBalance()
  },
  methods: {
    async getBalance () {
      const {data: res} = await this.$http.get('/property_management/user/getBalance', {params: {'id': this.form.id}})
      this.form.balance = res.balance
    },
    async recharge () {
      const {data: res} = await this.$http.get('/property_management/user/recharge', {params: {'id': this.form.id, 'money': this.form.money}})
      this.$message.success(res.msg)
      this.getBalance()
    }
  }
}
</script>

<style scoped lang="less">
.el-card{
  margin-top: 15px;
}
</style>



