<template>
  <div>
    <h3>欢迎<span>{{user}}</span></h3>
    <el-carousel height="350px">
      <el-carousel-item v-for="item in images" :key="item"><!-- 走马灯 -->
        <template slot-scope="scope">
          <el-image
            :src="baseUrl+item.url"
            style="width: 100%;height: 100%">

          </el-image>
        </template>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
export default {
  name: 'Welcome',
  created () {
    this.user = window.sessionStorage.getItem('username')
    this.baseUrl = this.$baseUrl
  },
  data () {
    return {
      user: '',
      images: [
        {
          url: '/image/1.jpg'
        },
        {
          url: '/image/2.jpg'
        },
        {
          url: '/image/3.jpg'
        }
      ],
      baseUrl: ''
    }
  }
}
</script>

<style scoped>

</style>



