import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/Login'
import UserHome from '../components/user/UserHome'
import UserWelcome from '../components/user/Welcome'
import UserPark from '../components/user/Park'
import UserFee from '../components/user/Fee'
import UserRepair from '../components/user/Repair'
import UserReturn from '../components/user/Return'
import UserAnnouncement from '../components/user/Announcement'
import UserComplaints from '../components/user/Complaints'
import Recharge from '../components/user/Recharge'

import AdminHome from '../components/admin/AdminHome'
import AdminAnnouncement from '../components/admin/Announcement'
import AdminHouse from '../components/admin/House'
import AdminRepair from '../components/admin/Repair'
import AdminReturn from '../components/admin/Return'
import AdminWelcome from '../components/admin/Welcome'
import AdminUser from '../components/admin/User'
import AdminComplaints from '../components/admin/Complaints'
import House1 from '../components/admin/House1'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', redirect: '/login' },
    { path: '/login', component: Login },
    {
      path: '/userhome',
      component: UserHome,
      redirect: '/userwelcome',
      children: [
        // eslint-disable-next-line standard/object-curly-even-spacing
        { path: '/userwelcome', component: UserWelcome},
        // eslint-disable-next-line standard/object-curly-even-spacing
        { path: '/userpark', component: UserPark},
        // eslint-disable-next-line standard/object-curly-even-spacing
        { path: '/userfee', component: UserFee},
        // eslint-disable-next-line standard/object-curly-even-spacing
        { path: '/userrepair', component: UserRepair},
        // eslint-disable-next-line standard/object-curly-even-spacing
        { path: '/userreturn', component: UserReturn},
        // eslint-disable-next-line standard/object-curly-even-spacing
        { path: '/userannouncement', component: UserAnnouncement},
        // eslint-disable-next-line standard/object-curly-even-spacing
        { path: '/usercomplaints', component: UserComplaints},
        { path: '/userrecharge', component: Recharge }
      ]
    },
    {
      path: '/adminhome',
      component: AdminHome,
      redirect: '/adminwelcome',
      children: [
        // eslint-disable-next-line standard/object-curly-even-spacing
        { path: '/adminwelcome', component: AdminWelcome },
        { path: '/adminannouncement', component: AdminAnnouncement },
        { path: '/adminhouse', component: AdminHouse },
        { path: '/adminrepair', component: AdminRepair },
        { path: '/adminreturn', component: AdminReturn },
        { path: '/adminuser', component: AdminUser },
        { path: '/admincomplaints', component: AdminComplaints },
        { path: '/adminhouse1', component: House1 }
      ]
    }
  ]
})



